import Vue from 'vue'
import VueRouter from 'vue-router'

import login from '@/views/login.vue'
import register from '@/views/register.vue'
import layout from '@/views/layout.vue'
import detail from '@/views/detail.vue'
import article from '@/views/second/article.vue'
import collect from '@/views/second/collect.vue'
import like from '@/views/second/like.vue'
import user from '@/views/second/user.vue'
import { getToken } from '@/utils/storage'
import { Toast } from 'vant'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/layout' },
  { path: '/login', component: login },
  { path: '/register', component: register },
  { path: '/detail/:id', component: detail },
  {
    path: '/layout',
    component: layout,
    children: [
      { path: '/', redirect: '/article' },
      { path: '/article', component: article },
      { path: '/collect', component: collect },
      { path: '/like', component: like },
      { path: '/user', component: user }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 路由导航守卫---前置
const arr = ['/login', '/register']
router.beforeEach((to, from, next) => {
  // console.log(to, from)
  if (getToken() || arr.includes(to.path)) {
    next()
  } else {
    Toast.fail('请先登录')
    next('/login')
  }
})

export default router
