<template>
  <div class="article-page">
    <!-- 页内顶部导航 -->
    <van-nav-bar title="收藏列表" fixed></van-nav-bar>

    <!-- 文章列表区域：上拉加载更多 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <ArticleItem
        v-for="item in articleList"
        :key="item.id"
        :curItem="item"
        @click.native="$router.push(`/detail/${item.id}`)"
      ></ArticleItem>
    </van-list>
  </div>
</template>

<script>
import ArticleItem from '@/components/ArticleItem.vue'
import { getCollectList } from '@/api/article'

export default {
  components: { ArticleItem },
  name: 'collect-page',
  data() {
    return {
      // 1.当前页
      page: 1,
      // 2.每页数量
      pageSize: 10,
      // 4.文章列表
      articleList: [],
      // 5.加载状态：加载动画、加载完毕
      loading: false,
      finished: false
    }
  },
  methods: {
    // 获取文章列表, 或加载下一页
    async onLoad() {
      // (1) 请求列表
      const data = await getCollectList({
        page: this.page++, // 当前页加载后，自动把页码加1，便于加载下一页
        pageSize: this.pageSize
      })
      // console.log(data)
      // (2) 下一页：注意解构
      this.articleList.push(...data.data.rows)
      // (3) 下一页加载完成，关闭动画
      this.loading = false
      // (4) 数据全部加载完成, 结束加载状态
      if (data.data.rows.length === 0) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.article-page {
  margin-bottom: 50px;
  margin-top: 44px;
  .my-nav {
    height: 44px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    background: #fff;
    display: flex;
    align-items: center;
    > a {
      color: #999;
      font-size: 14px;
      line-height: 44px;
      margin-left: 20px;
      position: relative;
      transition: all 0.3s;
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 0;
        height: 2px;
        background: #222;
        transition: all 0.3s;
      }
      &.active {
        color: #222;
        &::after {
          width: 14px;
        }
      }
    }
    .logo {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      > img {
        width: 64px;
        height: 28px;
        display: block;
        margin-right: 10px;
      }
    }
  }
}
</style>
