<template>
  <div class="article-page">
    <!-- 页内顶部导航 -->
    <nav class="my-nav van-hairline--bottom">
      <a
        href="javascript:;"
        class="active"
        @click="doCheck($event, 'weight_desc')"
        >推荐</a
      >
      <a href="javascript:;" @click="doCheck($event, '')">最新</a>
      <div class="logo"><img src="@/assets/logo.png" alt="" /></div>
    </nav>

    <!-- 文章列表区域：上拉加载更多 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <ArticleItem
        v-for="item in articleList"
        :key="item.id"
        :curItem="item"
        @click.native="$router.push(`/detail/${item.id}`)"
      ></ArticleItem>
    </van-list>
  </div>
</template>

<script>
import ArticleItem from '@/components/ArticleItem.vue'
import { getArticleList } from '@/api/article'

export default {
  components: { ArticleItem },
  name: 'article-page',
  data() {
    return {
      // 1.当前页
      current: 1,
      // 2.每页数量
      pageSize: 10,
      // 3.排序方式（推荐：'weight_desc'，最新：''）
      sorter: 'weight_desc',
      // 4.文章列表
      articleList: [],
      // 5.加载状态：加载动画、加载完毕
      loading: false,
      finished: false
    }
  },
  methods: {
    // 获取文章列表, 或加载下一页
    async onLoad() {
      // (1) 请求列表
      const data = await getArticleList({
        current: this.current++, // 当前页加载后，自动把页码加1，便于加载下一页
        pageSize: this.pageSize,
        sorter: this.sorter
      })
      // console.log(data)
      // (2) 下一页：注意解构
      this.articleList.push(...data.data.rows)
      // (3) 下一页加载完成，关闭动画
      this.loading = false
      // (4) 数据全部加载完成, 结束加载状态
      if (data.data.rows.length === 0) {
        this.finished = true
      }
    },
    // 切换顶部栏
    doCheck(el, sorter) {
      // 切换当前a标签的样式
      el.target.parentElement.querySelectorAll('a').forEach((e) => {
        if (e.classList.contains('active')) {
          e.classList.remove('active')
        }
      })
      el.target.classList.add('active')

      // 修改排序方式
      this.sorter = sorter
      // 清空当前页面
      this.articleList = []
      this.current = 1
      // 重新请求、渲染数据
      this.loading = true
      this.onLoad()
    },
    doDetail($event) {
      console.log($event)
    }
  }
}
</script>

<style lang="less" scoped>
.article-page {
  margin-bottom: 50px;
  margin-top: 44px;
  .my-nav {
    height: 44px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    background: #fff;
    display: flex;
    align-items: center;
    > a {
      color: #999;
      font-size: 14px;
      line-height: 44px;
      margin-left: 20px;
      position: relative;
      transition: all 0.3s;
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 0;
        height: 2px;
        background: #222;
        transition: all 0.3s;
      }
      &.active {
        color: #222;
        &::after {
          width: 14px;
        }
      }
    }
    .logo {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      > img {
        width: 64px;
        height: 28px;
        display: block;
        margin-right: 10px;
      }
    }
  }
}
</style>
