import request from '@/utils/request'

// 1. 获取首页面经列表
export const getArticleList = ({ current, pageSize, sorter }) => {
  return request.get('/h5/interview/query', {
    params: {
      current,
      pageSize,
      sorter
    }
  })
}

// 2. 获取单个面经详情
export const getArticleInfo = (id) => {
  return request.get('/h5/interview/show', { params: { id } })
}

// 3. 切换点赞
export const changeLike = (id) => {
  return request.post('/h5/interview/opt', {
    id,
    optType: 1
  })
}

// 4. 切换收藏
export const changeCollect = (id) => {
  return request.post('/h5/interview/opt', {
    id,
    optType: 2
  })
}

// 5.获取收藏列表
export const getCollectList = ({ page, pageSize }) => {
  return request.get('/h5/interview/opt/list', {
    params: {
      page,
      pageSize,
      optType: 2
    }
  })
}

// 6.获取喜欢列表
export const getLikeList = ({ page, pageSize }) => {
  return request.get('/h5/interview/opt/list', {
    params: {
      page,
      pageSize,
      optType: 1
    }
  })
}
