// 网络层API主要用于封装接口文档，其封装目的在于让.vue(业务层) 和 网络层分离
// 该模块主要用于封装与与用户相关的登录、注册、
// 按需导入、导出

import request from '@/utils/request'

// 登录
// 参数解构赋值：阅读性高、容错率高
export const UserLogin = ({ username, password }) => {
  return request.post('/h5/user/login', { username, password })
}

// 注册
export const UserRegister = ({ username, password }) => {
  return request.post('/h5/user/register', { username, password })
}

// 获取用户详情
export const getUserInfo = () => request.get('/h5/user/currentUser')
