<template>
  <div class="login-page">
    <!-- 顶部栏 -->
    <van-nav-bar title="面经登录" />
    <!-- 表单 -->
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名："
        placeholder="请输入用户名"
        :rules="[
          { required: true, message: '请输入用户名' },
          { pattern: /^\w{6,15}$/, message: '用户名6-15位' }
        ]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码："
        placeholder="请输入密码"
        :rules="[
          { required: true, message: '请输入密码' },
          { pattern: /^\w{6,15}$/, message: '用户名6-15位' }
        ]"
      />
      <div style="margin: 16px">
        <van-button block type="primary" native-type="submit">登录</van-button>
      </div>
    </van-form>
    <!-- 跳转 -->
    <a href="#/register" class="link">无账号？去注册</a>
    <!-- <router-link to="/login" class="link">有账号，去登录</router-link> -->
  </div>
</template>

<script>
import { UserLogin } from '@/api/user'
import { setToken } from '@/utils/storage'
export default {
  name: 'login-page',
  data() {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    async onSubmit(values) {
      console.log('submit', values)
      try {
        const data = await UserLogin(values)
        setToken(data.data.token)
        this.$router.push('/')
        this.$toast.success('登录成功')
        // console.log(data)
      } catch (err) {
        // console.log(err)
        this.$toast.fail(err.response.data.message)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.link {
  color: #069;
  font-size: 12px;
  padding-right: 20px;
  float: right;
}
</style>
